import { create } from "zustand";

type Language = "ky" | "ru";

type Translation = {
  [key: string]: {
    ky: string;
    ru: string;
  };
};

type LanguageStore = {
  language: Language;
  setLanguage: (lang: Language) => void;
  t: (key: string) => string;
};

const translations: Translation = {
  loading: {
    ky: "Жүктөлүүдө...",
    ru: "Загрузка...",
  },
  error_loading: {
    ky: "Маалыматты жүктөөдө ката кетти",
    ru: "Ошибка при загрузке данных",
  },
  teacher: {
    ky: "Мугалим",
    ru: "Учитель",
  },
  experience: {
    ky: "Тажрыйба",
    ru: "Опыт",
  },
  education: {
    ky: "Билими",
    ru: "Образование",
  },
  share: {
    ky: "Бөлүшүү",
    ru: "Поделиться",
  },
  successes: {
    ky: "Ийгиликтери",
    ru: "Успехи",
  },
  // Башка которуулар
};

export const useLanguageStore = create<LanguageStore>((set, get) => ({
  language: "ky", // Баштапкы тил
  setLanguage: (lang: Language) => set({ language: lang }),
  t: (key: string) => {
    const { language } = get();
    return translations[key]?.[language] || key;
  },
}));

export const useLanguage = () => {
  const { language, setLanguage, t } = useLanguageStore();
  return { language, setLanguage, t };
};
